export function getTacticName (tactic) {
    switch (tactic) {
        case 'without':
            return ' ';
        case 'one_for_all':
            return '  ';
        case 'question_bet':
            return '  ';
        case 'all_in':
            return '-';
        case 'team_bet':
            return '  ';
        default:
            return ' ';
    }
}